/*=========    
Global CSS
=========*/

body{
  background: none;
}

*{
  font-family: 'Open Sans', sans-serif;
}

h1{
  font-family: 'Nunito', sans-serif;
}

html, body, #app, #root>div {
  height: 100%
}

/*=========    
ANIMATIONS
=========*/

/* TV */

.TV{
  transition: all 1s ease-in-out;
}

.TV-hide{
  border-width: 0rem !important;
  padding: 0rem !important;
  -webkit-filter: grayscale(0%) !important;
  filter: grayscale(0%) !important;
}

/* Animated dial */

.animate-dial{
  transition: -webkit-transform .7s ease-in-out;
  transition: transform .7s ease-in-out;
  transition: transform .7s ease-in-out, -webkit-transform .7s ease-in-out;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.animate-dial:hover{
  -webkit-transform: rotate(-180deg);
  transform: rotate(-180deg);
}

/* Glow */

.glowing{
  transition: all .7s ease-in-out;
  -webkit-filter: drop-shadow(0px 0px 0.25rem #ffffff);
  filter: drop-shadow(0px 0px 0.25rem #ffffff);
  -webkit-animation: glow 1s ease-in-out infinite alternate;
  animation: glow 1s ease-in-out infinite alternate;
}

@keyframes glow {
  from {
    -webkit-filter: drop-shadow(0px 0px 0.25rem #fff1c1);
    filter: drop-shadow(0px 0px 0.25rem #fff1c1);
  }

  to {
    -webkit-filter: drop-shadow(0px 0px 2rem #fff1c1);
    filter: drop-shadow(0px 0px 2rem #fff1c1);
  }
}

@-webkit-keyframes glow {
  from {
    -webkit-filter: drop-shadow(0px 0px 0.25rem #ffffff);
    filter: drop-shadow(0px 0px 0.25rem #ffffff);
  }

  to {
    -webkit-filter: drop-shadow(0px 0px 2rem #ffffff);
    filter: drop-shadow(0px 0px 2rem #ffffff);
  }
}

/* Noise from https://codepen.io/stephen1on1/pen/VeyNZo */

.noisy {
  background-image: url(data:image/png;base64,);
}

/* Wavy background */

.bg-wavy {
  background: linear-gradient(275deg, #66ac0a, #ffe600, #66ac0a);
  background-size: 1200% 1200%;
  -webkit-animation: WavyAnimation 14s ease infinite;
  animation: WavyAnimation 14s ease infinite;
}

@-webkit-keyframes WavyAnimation {
  0%{
    background-position:0% 50%
  }

  50%{
    background-position:100% 50%
  }

  100%{
    background-position:0% 50%
  }
}

@keyframes WavyAnimation {
  0%{
    background-position:0% 50%
  }

  50%{
    background-position:100% 50%
  }

  100%{
    background-position:0% 50%
  }
}

/* CRT effect from http://aleclownes.com/2017/02/01/crt-display.html */

.crt-text {
  -webkit-animation: textShadow 10s infinite;
  animation: textShadow 10s infinite;
}

.crt-grid{
  color: #f1cc5d
}

.crt-grid::after {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(18, 16, 16, 0.1);
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  /*animation: flicker 7s infinite;*/
}

.crt-grid::before {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.25) 50%), linear-gradient(90deg, rgba(255, 0, 0, 0.06), rgba(0, 255, 0, 0.02), rgba(0, 0, 255, 0.06));
  z-index: 2;
  background-size: 100% 2px, 3px 100%;
  pointer-events: none;
}

